<template>
  <div>
    <Meter />
    
    <div class="my-5 row justify-content-center" >
      <img v-if="cart.carts.length===0" class="text-center" height="600" src="@/assets/images/shop.jpg" alt="">
      <form class="col-md-8" v-if="cart.carts.length!==0">
         <h4 class="text-center">訂購資料</h4>
        <div class="my-5 row justify-content-center" >
         
          <table class="table col-md-9">
            <thead>
              <th></th>
          
              <th>品名</th>
              <th>數量</th>
              <th>單價</th>
            </thead>
            <tbody>
              <tr v-for="item in cart.carts" :key="item.id">
                <td class="align-middle">
                  <button
                    type="button"
                    class="btn btn-outline-danger btn-sm"
                    @click="removeCart(item.id)"
                  >
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
                <!-- <td class="align-middle"> 
                  <img :src="product.imageUrl" class="img-fluid" alt />
                  </td> -->
                <td class="align-middle">
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon">已套用優惠券</div>
                </td>
                <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                <td class="align-middle">{{ item.final_total }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="3" class="text-right">總計</td>
                <td>{{ cart.total }}</td>
              </tr>
              <tr v-if="cart.final_total !== cart.total">
                <td colspan="3" class="text-right text-success">折扣價</td>
                <td class="text-success">{{ cart.final_total }}</td>
              </tr>
            </tfoot>
          </table>
          <div class="input-group mb-3 input-group-sm col-md-6">
            <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼" />
            <div class="input-group-append">
              <button class="btn btn-danger" type="button" @click="addCouponCode()">套用優惠碼</button>
            </div>
          </div>

          <form class="col-md-9" @submit.prevent="createOrder">
            <br/><br/>
            <h4 class="text-center">訂購人</h4>
            <div class="form-group">
              <label for="useremail">Email</label>
              <input
                type="email"
                class="form-control"
                name="email"
                id="useremail"
                v-validate="'required|email'"
                v-model="form.user.email"
                :class="{'is-invalid':errors.has('email')}"
                placeholder="請輸入 Email"
              />
              <span class="text-danger" v-if="errors.has('email')">email不能為空</span>
            </div>

            <div class="form-group">
              <label for="username">收件人姓名</label>
              <input
                type="text"
                class="form-control"
                :class="{'is-invalid':errors.has('name')}"
                name="name"
                id="username"
                v-model="form.user.name"
                v-validate="'required'"
                placeholder="輸入姓名"
              />
              <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
            </div>

            <div class="form-group">
              <label for="usertel">收件人電話</label>
              <input
                type="tel"
                class="form-control"
                :class="{'is-invalid':errors.has('tel')}"
                name="tel"
                id="usertel"
                v-model="form.user.tel"
                placeholder="請輸入電話"
              />
              <span class="text-danger" v-if="errors.has('tel')">電話欄位不得留空</span>
            </div>

            <div class="form-group">
              <label for="useraddress">收件人地址</label>
              <input
                type="text"
                class="form-control"
                :class="{'is-invalid':errors.has('address')}"
                name="address"
                id="useraddress"
                v-model="form.user.address"
                placeholder="請輸入地址"
              />
              <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
            </div>

            <div class="form-group">
              <label for="comment">留言</label>
              <textarea
                name
                id="comment"
                class="form-control"
                cols="30"
                rows="10"
                v-model="form.message"
              ></textarea>
            </div>

            <div class="text-right">
              <button class="btn btn-danger" @click.prevent="createOrder">送出訂單</button>
            </div>
          </form>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
import Meter from "@/components/Meter";
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
    Meter
  },

  data() {
    return {
      product: {},
      // pagination: {},
      status: {
        loadingItem: ""
      },

      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        }
      },

      coupon_code: []
    };
  },

  methods: {
    ...mapActions("productsModules", ["getProducts"]),
    ...mapActions("cartModules", ["getCart", "removeCart"]),

    getProduct(id) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then(response => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        console.log(response);
        vm.status.loadingItem = "";
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty
      };
      this.$http.post(url, { data: cart }).then(response => {
        //console.log(response);
        vm.status.loadingItem = "";
        vm.getCart();
        $("#productModal").modal("hide");
      });
    },

    addCouponCode() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`;
      const coupon = {
        code: vm.coupon_code
      };
      vm.$store.dispatch("updateLoading", true);
      this.$http.post(url, { data: coupon }).then(response => {
        console.log(response);
        vm.getCart();
        vm.$store.dispatch("updateLoading", false);
      });
    },

    createOrder() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
      const order = vm.form;
      vm.$store.dispatch("updateLoading", true);
      this.$validator.validate().then(result => {
        if (result) {
          // 當驗證成功時執行 AJAX 的行為
          this.$http.post(url, { data: order }).then(response => {
            console.log("訂單已建立", response);
            if (response.data.success) {
              vm.$router.push(`/customer_checkout/${response.data.orderId}`);
            }
            vm.$store.dispatch("updateLoading", false);
          });
        } else {
          // 驗證失敗產生的行為
          vm.$store.dispatch("updateLoading", false);
          console.log("欄位不完整");
        }
      });
    }
  },
  computed: {
    ...mapGetters("cartModules", ["cart"]),
    ...mapGetters("productsModules", ["categories", "products"])
  },

  created() {
    this.getProducts();
    this.getCart();
  }
};
</script>